import React, { useEffect, useState } from 'react'
import { Button, Dropdown } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import AddWebsiteModal from './AddWebsiteModal';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
import { CREATE_WEBSITE, DELETE_WEBSITE, UPDATE_WEBSITE } from '../../../../../actions/websiteAction';
import { CREATE_SHOPON, DELETE_SHOPON, UPDATE_SHOPON } from '../../../../../actions/shoponAction';
import { CREATE_EMAIL, DELETE_EMAIL, UPDATE_EMAIL } from '../../../../../actions/EmailCampaignAction';
import { TOGGLE_ADD_EMAIL, TOGGLE_ADD_SHOPON, TOGGLE_ADD_WEBSITE } from '../../../../../reducer/keys';
import { checkPermission } from '../../../../../actions/collaboratorActions';

const WebsiteSwitcher = ({ contentList, changeWebsite, selectedContent, title, isWebsite, method, addNew, noList }) => {
    const [addContent, setAddContent] = useState(false);
    const [editContent, setEditContent] = useState([]);
    const [spinner, setSpinner] = useState(false)

    const dispatch = useDispatch()
    const navigate = useNavigate()

    const editWebsite = (item) => {
        setEditContent(item);
        setAddContent(true)
    }

    useEffect(() => {
        if (addNew) { setAddContent(true) }
    }, [addNew])

    const addWebsite = (name) => {
        setSpinner(true)
        if (title.includes('Website')) dispatch(CREATE_WEBSITE(name, getResponse, navigate))
        if (title.includes('ShopOn')) dispatch(CREATE_SHOPON(name, getResponse, navigate))
        if (title.includes('Email')) dispatch(CREATE_EMAIL(name, getResponse, navigate))
    }

    const updateWebsite = (id, name) => {
        setSpinner(true)
        if (title.includes('Website')) dispatch(UPDATE_WEBSITE(id, name, getResponse))
        if (title.includes('ShopOn')) dispatch(UPDATE_SHOPON({ action: 1, id: id, name: name }, getResponse))
        if (title.includes('Email')) dispatch(UPDATE_EMAIL(id, name, getResponse))
    }


    const getResponse = (res, message, type) => {
        setAddContent(false)
        setEditContent([])
        if (title.includes('ShopOn') || title.includes('Email') || title.includes('Website')) {
            if (method) method(type ? type : '')
            handleClose()
            setSpinner(false)
        }
        //    if (res == 200) this.setState({ error: message, gallery_popoverShow: false }, () => this.props.TOGGLE_ADD_WEBSITE())
    }

    const handleClose = () => {
        setEditContent([]);
        setAddContent(false);
        if (title.includes('ShopOn')) dispatch({ type: TOGGLE_ADD_SHOPON, payload: false })
        if (title.includes('Email')) dispatch({ type: TOGGLE_ADD_EMAIL, payload: false })
        if (title.includes('Website')) dispatch({ type: TOGGLE_ADD_WEBSITE, payload: false })
    }


    const deleteWebsite = (id) => {
        Swal.fire({
            title: "Are You Sure?",
            text: `Are you sure you want to permanently delete this ${title.toLowerCase()} template?`,
            icon: "warning",
            showCancelButton: true,
            cancelButtonText: "Cancel",
            confirmButtonText: "Yes, Delete it",

        }).then((result) => {
            if (result.value) {
                if (title.includes('Website')) dispatch(DELETE_WEBSITE(id, getResponse, navigate))
                if (title.includes('ShopOn')) dispatch(DELETE_SHOPON(id, getResponse, navigate))
                if (title.includes('Email')) dispatch(DELETE_EMAIL(id, getResponse, navigate))
            }
        });
    }



    return (<>
        {!noList ? <Dropdown className='w-100'>
            {selectedContent ? <Dropdown.Toggle id="web_switcher_btn" variant="secondary" size={'sm'} className="d-flex align-items-center w-100 justify-content-between p-1">
                <div className="d-flex align-items-center flex-grow-1">
                    <div className="symbol symbol-24 radius-2 flex-shrink-0 me-1 fs-5">
                        <FontAwesomeIcon icon={'browser'} />
                    </div>
                    <div className="overflow-hidden" style={{ maxWidth: 130 }}>
                        <p className="fw-semibold mb-0 text-ellipsis fs-9 text-start">{selectedContent?.name}</p>
                        {selectedContent?.totalPost && <p className='mb-0 fs-10 text-muted'>{selectedContent?.totalPost} Posts</p>}
                    </div>
                </div>
            </Dropdown.Toggle> : <div className="w-100 sk_rect rounded-1 h-34px"></div>}
            {/* <div className="w-100 sk_rect rounded-1 h-34px"></div> */}
            <Dropdown.Menu className="w-100">
                <div className="dropdown-height">
                    {contentList?.map((item, index) => {
                        const data = isWebsite ? item?.wall?.Wall : item
                        return <div key={index} className='position-relative mb-1'>
                            <Dropdown.Item eventKey={data?.id} onClick={() => checkPermission('publish', title.includes('Website') ? 'website' : title.includes('ShopOn') ? 'shopon' : 'email_camp') ? changeWebsite(data) : {}} className={data?.id == selectedContent?.id ? 'active' : ''}>
                                <div className='menu-title'>{data?.name}</div>
                            </Dropdown.Item>
                            <div className='dropdown-action'>
                                <Button variant='dropdown-icon' className='web_edit' onClick={() => checkPermission('publish', title.includes('Website') ? 'website' : title.includes('ShopOn') ? 'shopon' : 'email_camp', true) ? editWebsite(isWebsite ? item?.wall?.Wall : item) : {}}>
                                    <FontAwesomeIcon icon={'ellipsis-vertical'} iconClass={'me-0'}/>
                                </Button>
                            </div>
                        </div>
                    })}
                    <div className='mt-2 pt-2 border-top'>
                        <Dropdown.Item id="add_web_btn" onClick={() => checkPermission('publish', title.includes('Website') ? 'website' : title.includes('ShopOn') ? 'shopon' : 'email_camp', true) ? setAddContent(true) : {}} className='f-center'>
                            <FontAwesomeIcon icon={'plus'} />
                            <span className='menu-title flex-grow-0 ms-1'>New {title}</span>
                        </Dropdown.Item>
                    </div>
                </div>
            </Dropdown.Menu>
        </Dropdown> : null}
        <AddWebsiteModal show={addContent} title={title} onCreate={addWebsite} onUpdate={updateWebsite} editContent={editContent} handleClose={handleClose} contentList={contentList} spinner={spinner} onDelete={deleteWebsite} />
    </>
    )
}


export default WebsiteSwitcher