import React, { useEffect, useState } from 'react';
import { Modal, Form, Button } from 'react-bootstrap';

const AddWebsiteModal= ({ show, title, onCreate, onUpdate, editContent, handleClose, isStatus, contentList, spinner, onDelete }) => {
    const [name, setName] = useState(editContent?.name);
    const [sameName, setSameName] = useState(false)
    const [autoUpdate, setAutoUpdate] = useState(editContent?.autoUpdate);
    const [status, SetStatus] = useState(editContent?.status);
    const [error, SetError] = useState(false);

    const closePopup = () => {
        setName('');
        handleClose('');
    };

    const onSave = () => {
        if (name && name.length > 0){
            if(contentList && contentList.length > 0){
                const exist = checkName(name)
                if(exist.length > 0) setSameName(true)
                else onCreate(name)
            } 
            else onCreate(name);
        } 
        else SetError(true);
    };

    const checkName = (name) => {
        const exist = contentList.filter(list => String(title == 'Website' ? list.wall.Wall.name : list.name).toLowerCase().trim() == String(name).toLowerCase().trim())
        return exist
    }

    const Update = (id) => {
        if (name && name.length > 0){
            if(contentList && contentList.length > 0){
                const exist = checkName(name)
                if(exist.length > 0) setSameName(true)
                else onUpdate(id, name, status, autoUpdate);
            }
            else onUpdate(id, name, status, autoUpdate);
        } 
        else SetError(true);
    };

    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            if (editContent && Object.keys(editContent).length > 0) {
                Update(editContent.id);
            } else {
                onSave();
            }
        }
    };

    useEffect(() => {
        setName(editContent?.name);
        setAutoUpdate(editContent?.autoUpdate);
        SetStatus(editContent?.status);
        SetError(false)
        setSameName(false)
    }, [editContent]);

    return (
        <Modal show={show} size='sm' onHide={handleClose} id="gallery_modal" centered>
            <Modal.Header closeButton>
                <Modal.Title>
                    {editContent && Object.keys(editContent).length > 0 ? 'Edit' : 'Create'} {title}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form.Group className='mb-4'>
                    <Form.Control id="g_name" className={`fw-semibold  ${error || sameName ?'is-invalid':''}`} placeholder={`Enter ${title} Name`} value={name} onChange={(e) => setName(e.target.value)} onKeyPress={handleKeyPress} />
                    {error || sameName ?<span className='invalid-feedback'>{sameName ? 'Name already exists.' : `${title} name is required`}</span>: ''}
                </Form.Group>
                {isStatus && editContent && Object.keys(editContent).length > 0 ? (
                    <>
                    <Form.Group className="f-between mb-4">
                        <Form.Label htmlFor='gallery_a_u'>Auto Update</Form.Label>
                        <Form.Check type="switch" id="gallery_a_u" defaultChecked={autoUpdate} onChange={() => setAutoUpdate(!autoUpdate)} />
                    </Form.Group>
                    <Form.Group className="f-between mb-4">
                        <Form.Label htmlFor='gallery_s'>Status</Form.Label>
                        <Form.Check type="switch" id="gallery_s" defaultChecked={status} onClick={() => SetStatus(!status)} />
                    </Form.Group>
                    </>
                ) : (
                null
                )}
            </Modal.Body>
            <Modal.Footer>
                {editContent && Object.keys(editContent).length > 0 ? <>
                    <Button variant='outline-danger' size='sm' id='web_close_btn' className="me-3" onClick={()=>onDelete(editContent.id)}>Delete</Button>
                    <Button size='sm' id='web_update_btn' className={spinner ? 'spinner' : ''} onClick={() => Update(editContent.id)}>Update</Button>
                    </> : 
                    <Button size='sm' id='web_save_btn' className={spinner ? 'spinner' : ''} onClick={onSave}>Save</Button>
                }
            </Modal.Footer>
        </Modal>
    );
};

export default AddWebsiteModal
