import React, { useEffect, useState } from 'react';
import { Modal, Form, Button } from 'react-bootstrap';

const AddContentModal = ({ show, title, apiError, setApiError, onCreate, onUpdate, editContent, handleClose, isStatus , deleteContent, spinner}) => {
    const [name, setName] = useState('');
    const [autoUpdate, setAutoUpdate] = useState(1);
    const [status, setStatus] = useState(1);
    const [error, setError] = useState(false);

    const closePopup = () => {
        setName('');
        handleClose('');
    };

    const onSave = () => {
        if (name && name.length > 0){ setError(false) ; onCreate(name,autoUpdate,status)}
        else setError(true);
    };

    const Update = (id) => {
        if (name && name.length > 0){ setError(false) ;  onUpdate(id, name, status, autoUpdate)}
        else setError(true);
    };

    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            if (editContent && Object.keys(editContent).length > 0) {
                Update(editContent.id,  name, status, autoUpdate);
            } else {
                onSave();
            }
        }
    };

    useEffect(() => {
        if (editContent && Object.keys(editContent).length > 0) {
            setName(editContent.name);
            setAutoUpdate(Number(editContent.autoUpdate));
            setStatus(Number(editContent.status));
        } else {
            resetForm();
        }
    }, [editContent]);

    const resetForm = () => {
        setName('');
        setAutoUpdate(1);
        setStatus(1);
        setError(false);
    };

    return (
        <Modal show={show} size='sm' onHide={handleClose} id="cont_modal" centered>
            <Modal.Header closeButton>
                <Modal.Title>
                    {editContent && Object.keys(editContent).length > 0 ? 'Edit' : 'Create'} {title}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form.Group className='mb-4'>
                    <Form.Control id="g_name" className={`fw-semibold  ${error || apiError?'is-invalid':''}`} placeholder='Enter Gallery Name' value={name} onChange={(e) => { setError(false) ; setApiError('') ; setName(e.target.value)}} onKeyPress={handleKeyPress} />
                    <span className="invalid-feedback">{error ?  `${title} name is required` : apiError ? `${apiError}` :''}</span>
                </Form.Group>
                <Form.Group className="f-between mb-4">
                    <Form.Label htmlFor='cont_a_u'>Auto Update</Form.Label>
                    <Form.Check type="switch" id="cont_a_u" checked={autoUpdate} onChange={() => setAutoUpdate(Number(!autoUpdate))} />
                </Form.Group>
                <Form.Group className="f-between mb-4">
                    <Form.Label htmlFor='cont_s'>Status</Form.Label>
                    <Form.Check type="switch" id="cont_s" checked={status} onClick={() => setStatus(Number(!status))} />
                </Form.Group>
            </Modal.Body>
            <Modal.Footer>
               {editContent && Object.keys(editContent).length > 0 ? <>
                  <Button variant='outline-danger' size='sm'  id='con_delete_btn'  onClick={()=>deleteContent(editContent)}>Delete</Button>
                    <Button size='sm' id='con_update_btn'  className={`${spinner ? 'spinner':''}`} disabled={spinner}  onClick={() => Update(editContent.id)}>Update</Button>
                    </>
               : (
                    <Button size='sm' id='con_save_btn' className={`${spinner ? 'spinner':''}`} disabled={spinner} onClick={onSave}>Save</Button>
                )}
            </Modal.Footer>
        </Modal>
    );
};

export default AddContentModal;
