import React, { useEffect, useState } from 'react'
import { Button, Dropdown, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import AddContentModal from './AddContentModal';
import { CHANGE_MODERATION_WALLID, MODERATION_LODING, MODERATION_RESET, TOGGLE_ADD_GALLERY } from '../../../../../reducer/keys';
import { activeTabActionFilter, getModeration, removeAllFilterBody } from '../../../../../actions/moderationActions';
import { GET_FEEDS } from '../../../../../actions/manageFeedsAction';
import { closePricingModal } from '../../../../../utils/close_modal';
import { GALLERY_WALL_UPDATE } from '../../../../../actions/sceneActions';
import Swal from 'sweetalert2';
import { checkPermission } from '../../../../../actions/collaboratorActions';
import { SNAPUP_DETAILS_ACTION } from '../../../../../actions/addFeedActions';

const ContentSwitcher = ({ contentList, selectedContent, title, iswebsite, onlySwitcher, modifyContent, selectLoader, header }) => {
    const allWalls = useSelector(state => state.dashboardData?.dashboarddata?.allWalls)
    const user = useSelector(state => state.authorized?.userData?.user_data);
    const { wallsLoader, addGalleryNow } = useSelector(state => state?.dashboardData)

    const [selectedWall, setSelectedWall] = useState(null);
    const [addContent, setAddContent] = useState(false);
    const [editContent, setEditContent] = useState([]);
    const [loader, setLoader] = useState(true)
    const [spinner, setSpinner] = useState(false)
    const [apiError, setApiError] = useState(false)

    const dispatch = useDispatch()
    const navigate = useNavigate()

    const changeContent = (item) => {
        setSelectedWall(item);
        localStorage.setItem("wallId", item.id)
        if (title?.includes('Email') || title?.includes('Shop') || title?.includes('Website')) {
            modifyContent(item)
        }
        else {
            if (window.location.pathname?.includes('/more-apps')) {
                navigate(`/more-apps/${item.id}`);
            }

            else if (window.location.pathname?.includes('/moderation') || window.location.pathname?.includes('/reviewhub')) {
                dispatch({ type: CHANGE_MODERATION_WALLID, payload: true });
                dispatch({ type: MODERATION_RESET, payload: true });
                dispatch(GET_FEEDS())
                dispatch(removeAllFilterBody(true))
                dispatch(activeTabActionFilter(0))
                if(window.location.pathname?.includes('/moderation')) navigate(`/content/moderation/${item.id}`)  
                else dispatch(SNAPUP_DETAILS_ACTION())
                closePricingModal(dispatch)
            }
            else {
                dispatch({ type: CHANGE_MODERATION_WALLID, payload: true });
                dispatch({ type: MODERATION_RESET, payload: true });
                dispatch(GET_FEEDS())
                setTimeout(() => {
                    if (item?.totalPost > 0) navigate(`/content/managefeed/${item.id}`);
                    else navigate(`/content/addfeed/${item.id}`);
                }, 1000);
            }
        }


    }

    const callback = (msg) => {
        if (msg) {
            setApiError(msg)
            setSpinner(false)
        }
        else {
            openAddModal(false);
            setEditContent([])
            setSpinner(false)
        }
    };

    const openAddModal = (status) => { dispatch({ type: TOGGLE_ADD_GALLERY, payload: status }) }


    const isGalleryNameExist = (galleryName) => allWalls?.some(wall => wall?.name?.toLowerCase() === galleryName?.toLowerCase());

    const onAddGallery = (name, autoUpdate, status) => {
        const galleryName = name?.trim();

        if (isGalleryNameExist(galleryName)) {
            setApiError("Gallery name already exists.");
            return;
        }

        if (galleryName) {
            setSpinner(true);
            dispatch(GALLERY_WALL_UPDATE({ name: galleryName, status, autoUpdate: autoUpdate || 0, deleted: 0, id: '', action: 1 }, navigate, callback));
        }
    };


    const deleteContent = (item) => {
        Swal.fire({
            title: "Are You Sure?",
            text: 'You are about to delete a Gallery that is used in Publish. Deleting the gallery would also delete the corresponding Publish sections.',
            icon: "warning",
            showCancelButton: true,
            cancelButtonText: "Cancel",
            confirmButtonText: "Yes, Delete it",
        }).then((result) => {
            if (result.value) {
                dispatch(GALLERY_WALL_UPDATE({ name: item.name, status: item.status, autoUpdate: item.autoUpdate || 0, id: item.id, deleted: 0, action: 3 }, navigate, callback))
            }
            else if (result.dismiss === "cancel") { }
        });
    }

    const onUpdate = (id, galleryName, status, autoupdate) => {
        if (galleryName && galleryName.length > 0) {
            setSpinner(true)
            dispatch(GALLERY_WALL_UPDATE({ name: galleryName?.trim(), status: status, id: id, autoUpdate: autoupdate || 0, deleted: 0, action: 2 }, navigate, callback))
        }
    }

    const editGallery = (item) => {
        setEditContent(item);
        openAddModal(true)
    }

    const checkAddConsent = () => {
        if(window.location.href.includes('content/moderation')){
            if(checkPermission('contentGallery', 'editGallery', true)) openAddModal(true)
        }
        else {
            
            if(checkPermission('publish', title.includes('Website') ? 'website' : title.includes('Shop') ? 'shopon' : 'email_camp', true)) navigate(`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`)
        } 
    }

    const checkChangeConsent = (data) => {
        if(!title) changeContent(data)
        
        else{
            if(checkPermission('publish', title.includes('Website') ? 'website' : title.includes('Shop') ? 'shopon' : 'email_camp', true)) changeContent(data)  
        }
    }

    useEffect(() => {
        const getSelectedWall = allWalls?.find((wall) => wall.id == localStorage.getItem('wallId'))
        if ((selectedContent && Object.keys(selectedContent).length > 0)) {
            setSelectedWall(selectedContent)
            setLoader(false)
        } else if (selectLoader) setLoader(true)
        else {
            setSelectedWall(getSelectedWall ? getSelectedWall : allWalls && allWalls[0] ? allWalls[0] : '');
            setLoader(false)
        }
    }, [, selectedContent, selectLoader, allWalls, localStorage.getItem('wallId')])

    return (<><div className='d-flex align-items-center'>
        <Dropdown className='w-100'>
            {wallsLoader || loader ? <div className="w-100 sk_rect rounded-1 h-34px"></div> :
                selectedWall && Object.keys(selectedWall).length > 0 ? <Dropdown.Toggle id="content_btn" variant="secondary" size={'sm'} className="d-flex align-items-center w-100 justify-content-between" style={{ padding: header ? 2 : 4 }}>
                    <div className="d-flex align-items-center flex-grow-1">
                        <div className={`symbol symbol-${header ? '28' : '32'} radius-2 flex-shrink-0 me-2`}>
                            <span className="symbol-label fs-8">{(selectedWall?.name)?.charAt(0).toUpperCase()}</span>
                        </div>
                        <div className="overflow-hidden w-100 text-start d-flex flex-column" style={{ maxWidth: 130 }}>
                            <p className="fw-bold mb-0 text-ellipsis fs-9 lh-sm">{selectedWall?.name}</p>
                            <span className='fs-10 text-muted'>{selectedWall?.totalPost ? `${selectedWall?.totalPost} Posts` : '0 Post'}</span>
                        </div>
                    </div>
                </Dropdown.Toggle>
                    :
                    <Button variant='secondary' size='lg' className='w-100' onClick={() => checkAddConsent()}>
                        <FontAwesomeIcon icon={'plus'} iconClass={'me-1'} />
                        New Gallery
                    </Button>
            }

            {allWalls ? <Dropdown.Menu className="w-100 p-0">
                <div className="dropdown-height">
                    {allWalls?.map((item, index) => {
                        const data = iswebsite ? item?.wall?.Wall : item
                        return <div key={index} className='position-relative'>
                            <Dropdown.Item eventKey={data?.id} onClick={() => checkChangeConsent(data)} className={`mb-1 rounded-0 ${data?.id == localStorage.getItem('wallId') ? 'active' : ''}`}>
                                <div className="d-flex align-items-center">
                                    <div className={`symbol symbol-${header ? '28' : '32'} radius-2 flex-shrink-0 me-2`}>
                                        <span className="symbol-label fs-8">{(data?.name)?.charAt(0).toUpperCase()}</span>
                                    </div>
                                    <div className="overflow-hidden w-100 text-start d-flex flex-column" style={{ maxWidth: 130 }}>
                                        <p className="fw-bold mb-0 text-ellipsis fs-9 lh-sm">{data?.name}</p>
                                        <span className='fs-10 text-muted lh-sm'>{data?.totalPost ? `${data?.totalPost} Posts` : '0 Post'}</span>
                                    </div>
                                    <div>
                                    </div>
                                </div>
                            </Dropdown.Item>
                            {!onlySwitcher && <div className='dropdown-action pe-2'>
                                <Button variant='dropdown-icon' className='content_edit' onClick={() => checkPermission('contentGallery', 'editGallery', true, false, item.id) ? editGallery(item) : {}}>
                                    <FontAwesomeIcon icon={'ellipsis-vertical'} iconClass={'me-0'} />
                                </Button>
                            </div>}
                        </div>
                    })}
                    {!onlySwitcher && <div className='mt-2 p-2 border-top'>
                        <Dropdown.Item id="add_web_btn" onClick={() => checkPermission('contentGallery', 'createGallery', true) ? openAddModal(true) : {}} className='f-center'>
                            <span className="svg-icon svg-icon-9">
                                <FontAwesomeIcon icon={'plus'} />
                            </span>
                            <span className='menu-title flex-grow-0'>New {title}</span>
                        </Dropdown.Item>
                    </div>}
                </div>
            </Dropdown.Menu> : null}
        </Dropdown>
        {selectedWall && Object.keys(selectedWall).length > 1 && /\/(moderation|addfeed|managefeed|manual-upload)/.test(window.location.pathname) ?
            <OverlayTrigger placement='bottom' overlay={<Tooltip>Add more content</Tooltip>} >
                <button style={{ cursor: 'pointer' }} onClick={()=> checkPermission('networkManager', 'manageFeed', true) ? navigate(`/content/addfeed/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`) : {}} className="btn btn-primary btn-sm ms-1">
                    <FontAwesomeIcon icon={'plus'} iconClass={'me-0'} />
                </button>
            </OverlayTrigger>
            : null}
    </div>
        <AddContentModal show={addGalleryNow} apiError={apiError} setApiError={setApiError} title="Gallery" onCreate={onAddGallery} onUpdate={onUpdate} editContent={editContent} handleClose={() => { setEditContent([]); openAddModal(false); setApiError('') }} deleteContent={deleteContent} spinner={spinner} />
    </>
    )
}


export default ContentSwitcher